import { Col, Row, Collapse, Form, Input, Button, message } from 'antd';
import React from 'react';
import questionImg from '../../assets/Animations/question.png';
const { Panel } = Collapse;
const Help = () => {
  const text = `
  Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. 
`;
  const onFinish = () => {
    message.success('Sent');
  };
  return (
    <div className='help'>
      <div className='top'>
        <div className='container'>
          <Row>
            <Col md={16} className='flex-center'>
              <header>
                <h1>Need Help?</h1>
                <p>Our team is always here to help you out</p>
              </header>
            </Col>
            <Col md={8}>
              <img src={questionImg} alt='help ginnovation' />
            </Col>
          </Row>
        </div>
      </div>
      <div className='container content'>
        <Row gutter={24}>
          <Col md={16}>
            <h3>FAQs</h3>
            <p>Some frequently asked questions by students</p>
            <Collapse accordion>
              <Panel header='What is Ginnovation?' key='1'>
                <p>{text}</p>
              </Panel>
              <Panel header='Who can be a Ginnovation Member?' key='2'>
                <p>{text}</p>
              </Panel>
              <Panel header='How to join Ginnovation?' key='3'>
                <p>{text}</p>
              </Panel>
              <Panel header='How to LogIn to Ginnovation?' key='4'>
                <p>{text}</p>
              </Panel>
              <Panel header='How to upload data to profile?' key='5'>
                <p>{text}</p>
              </Panel>
              <Panel header='How to be a mentor?' key='6'>
                <p>{text}</p>
              </Panel>
              <Panel header='How to be a mentor?' key='7'>
                <p>{text}</p>
              </Panel>
              <Panel header='How to reset my password?' key='8'>
                <p>{text}</p>
              </Panel>
            </Collapse>
          </Col>
          <Col md={8}>
            <div className='issue-form'>
              <h3>Let Us Know</h3>
              <p>If you are facing any difficulty, feel free to mail us</p>
              <Form name='basic' onFinish={onFinish}>
                <Form.Item
                  name='title'
                  rules={[
                    { required: true, message: 'Please input your issue!' },
                  ]}
                >
                  <Input placeholder='What is the Issue?' />
                </Form.Item>

                <Form.Item
                  name='aboutIssue'
                  rules={[
                    {
                      required: true,
                      message: 'Please input about the issue!',
                    },
                  ]}
                >
                  <Input.TextArea
                    placeholder='Write something about the Issue'
                    rows={4}
                  />
                </Form.Item>
                <Form.Item>
                  <Button type='primary' htmlType='submit'>
                    Send
                  </Button>
                </Form.Item>
              </Form>
            </div>
          </Col>
        </Row>
      </div>
    </div>
  );
};

export default Help;
